import React, { forwardRef, useEffect, useRef, useState } from 'react';
import { bool, func, number, oneOfType, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-disclosure-text-button';
const CSS_STATE = 'vds-state';

const DisclosureTextButton = forwardRef(
  (
    {
      className,
      expanded,
      expandedText,
      focus,
      id,
      index,
      onBlur,
      onClick,
      onFocus,
      onKeyDown,
      text,
      ...remainingProps
    },
    ref
  ) => {
    const [focused, setFocused] = useState(focus);

    ref = ref || useRef(null);

    useEffect(() => {
      if (focus && ref.current) {
        ref.current.focus();
      }
      setFocused(focus);
    }, [focus, ref]);

    const handleFocus = event => {
      setFocused(true);
      if (typeof onFocus === 'function') {
        onFocus(event);
      }
    };

    const handleBlur = event => {
      setFocused(false);
      if (typeof onBlur === 'function') {
        onBlur(event);
      }
    };

    const handleClick = (event, id) => {
      if (typeof onClick === 'function') {
        onClick(event, id);
      }
    };

    const handleKeyDown = (event, id) => {
      if (typeof onKeyDown === 'function') {
        onKeyDown(event, id);
      }
    };

    return (
      <dt>
        <button
          aria-controls={`disclosure-content-${id}`}
          aria-expanded={expanded ? 'true' : 'false'}
          className={classnames(
            CSS_PREFIX,
            {
              [`${CSS_STATE}--active`]: Boolean(expanded),
              [`${CSS_STATE}--focus`]: Boolean(focused),
              'focus-visible': Boolean(focused)
            },
            className
          )}
          id={`disclosure-text-button-${id}`}
          onBlur={handleBlur}
          onClick={e => handleClick(e, index)}
          onFocus={handleFocus}
          onKeyDown={e => handleKeyDown(e, index)}
          ref={ref}
          {...remainingProps}
        >
          {expanded && expandedText ? expandedText : text}
        </button>
      </dt>
    );
  }
);

DisclosureTextButton.propTypes = {
  /**
   * @ignore
   */
  className: string,

  /**
   * Expanded
   */
  expanded: bool,

  /**
   * Text shown when expanded
   */
  expandedText: string,

  /**
   * Focus
   */
  focus: bool,

  /**
   * ID
   */
  id: oneOfType([number, string]),

  /**
   * Index
   */
  index: number,

  /**
   * Callback fired when the input element loses focus.
   */
  onBlur: func,

  /**
   * Callback fired when the input element is clicked on
   */
  onClick: func,

  /**
   * Callback fired when the input element is focused on.
   */
  onFocus: func,

  /**
   * Callback fired when the input element is clicked on
   */
  onKeyDown: func,

  /**
   * Text of the button
   */
  text: string
};

DisclosureTextButton.defaultProps = {
  expandedText: 'Hide Details',
  text: 'Show Details'
};

DisclosureTextButton.displayName = 'DisclosureTextButton';

export default DisclosureTextButton;
