import React, { Children, cloneElement, Fragment } from 'react';
import { bool, func, node, number, oneOfType, string } from 'prop-types';

const CSS_PREFIX = 'vds-disclosure-details';

const DisclosureDetailsItem = ({ children, expanded, focus, id, index, onClick, onKeyDown }) => (
  <Fragment>
    {Children.map(children, child => cloneElement(child, { className: CSS_PREFIX, expanded, focus, id, index, onClick, onKeyDown }))}
  </Fragment>
);

DisclosureDetailsItem.propTypes = {
  /**
   * @ignore
   */
  children: node,

  /**
   * Expanded
   */
  expanded: bool,

  /**
   * Focus
   */
  focus: bool,

  /**
   * ID
   */
  id: oneOfType([number, string]),

  /**
   * Index
   */
  index: number,

  /**
   * on Click
   */
  onClick: func,

  /**
   * on KeyDown
   */
  onKeyDown: func
};

DisclosureDetailsItem.displayName = 'DisclosureDetailsItem';

export default DisclosureDetailsItem;
