import React, { Children, cloneElement } from 'react';
import { bool, func, node, number, oneOfType, string } from 'prop-types';

const CSS_PREFIX = 'vds-disclosure-text';

const DisclosureTextItem = ({ children, expanded, focus, id, index, onClick, onKeyDown }) => (
  <div>
    {Children.map(children, child => cloneElement(child, { className: CSS_PREFIX, expanded, focus, id, index, onClick, onKeyDown }))}
  </div>
);

DisclosureTextItem.propTypes = {
  /**
   * @ignore
   */
  children: node,

  /**
   * Expanded
   */
  expanded: bool,

  /**
   * Focus
   */
  focus: bool,

  /**
   * ID
   */
  id: oneOfType([number, string]),

  /**
   * Index
   */
  index: number,

  /**
   * on Click
   */
  onClick: func,

  /**
   * on KeyDown
   */
  onKeyDown: func
};

DisclosureTextItem.displayName = 'DisclosureTextItem';

export default DisclosureTextItem;
